    }

    .Navbar_nav__1ebK_ > .Navbar_navBtn__1nD_y > label > span {
        display: block;
        width: 25px;
        height: 10px;
        border-top: 2px solid #eee;
    }

    .Navbar_nav__1ebK_ > .Navbar_navLinks__2mvCD {
        position: absolute;
        display: block;
        width: 100%;
        background-color: #333;
        height: 0px;
        transition: all 0.3s ease-in;
        overflow-y: hidden;
        top: 50px;
        left: 0px;
    }

    .Navbar_nav__1ebK_ > .Navbar_navLinks__2mvCD > a {
        display: block;
        width: 100%;
    }

    .Navbar_nav__1ebK_ > #Navbar_navCheck__h-i2I:not(:checked) ~ .Navbar_navLinks__2mvCD {
        height: 0px;
    }

    .Navbar_nav__1ebK_ > #Navbar_navCheck__h-i2I:checked ~ .Navbar_navLinks__2mvCD {
        height: calc(100vh - 50px);
        overflow-y: auto;
    }
}
